.navbar {
    width: 100vw;
    color: white;
}

.navbar-text {
    color: whitesmoke !important;
    text-decoration: none;
}

.alert {
    margin-bottom: 0;
}

#navbarLogo {
    border: solid 1px whitesmoke !important;
    padding: 5px;
    border-radius: .5rem;
    margin-right: 1rem;
    color: whitesmoke;
}

.outerCard {
    background-image: url("img/hangrylogo.jpg");
    background-position: center;
    background-size: contain;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.innerCard {
    background-color: rgba(245, 245, 245, 0);
    border: none;
    margin-bottom: 18rem;
}

#searchInput {
    border-radius: .5rem;
    text-align: center;
    background-color: rgba(248, 248, 255, 0.7);
}

#searchForm {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.submitBtn {
    margin-top: .2rem;
    padding: .3rem;
    background-color: rgba(245, 245, 245, 0.51);
    border: solid black .1rem;
    border-radius: .5rem;
}

.submitBtn:hover {
    border: solid whitesmoke .1rem;
}

.selectionCard {
    position: relative;
    top: 7rem;
    height: 1.5rem;
}

#navbarCode {
    margin-left: 2rem;
}